<template>
    <div class="login">
        <!-- <p>登录管理</p> -->
        <center>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="login_form">
            <el-form-item label="用户名" prop="username">
                <el-input v-model="ruleForm.username"></el-input>
            </el-form-item>

            <el-form-item label="密码" prop="password">
                <el-input type="password" v-model="ruleForm.password"></el-input>
            </el-form-item>

            <el-form-item class="btns">
                <el-button type="primary" @click="login">登录</el-button>
                <el-button  type="info" @click="resetLoginForm">重置</el-button>
            </el-form-item>
        </el-form>
        </center>
    </div>
</template>

<script>
  export default {
    name:"login",
    data() {
      return {
        ruleForm: {
          username: '',
          password: ''
        },
        rules: {
          username: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
            { min: 3, max: 18, message: '长度在 3 到 18 个字符', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 3, max: 18, message: '长度在 3 到 18 个字符', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      login() {
        let {username,password} = this.ruleForm;
        this.$http({
            method: 'post',
            url: '/User/login',
            data: {username:username,password:password}
        }).then(res=>{
            console.log("登录返回数据",res);
            let code = res.data.code;
            console.log("code数据",code);
            if(code == 200){
              //前端对数据的缓存一般常用的为两种：1.通过vuex（页面刷新数据会丢失）；2.本机存储(html5的新特性，页面刷新，数据还在)
              //登录成功之后，对用户名进行一个缓存处理操作(sessionStorage会在浏览器关掉后，里面的缓存就会自动清理了，localStorage不会，它会一直保留，需要手动清理)
              sessionStorage.setItem("username",username);  //本机存储 （用户名）
              sessionStorage.setItem("name",res.data.data.name); // 姓名
              this.$router.push('/home');
            }else{
              this.$message.error({
                message: res.data.msg
              })
            }
        })
      },
      //点击重置按钮 重置登录表单
      resetLoginForm(){
          this.$refs.ruleForm.resetFields();
      },
    }
  }
</script>

<style scoped>
    .login{
       background-color: #2b4b6b;
       height: 100vh;
       margin: auto;
       padding: 0;
    }

    .login_form{
        width: 30%;
        padding-top: 259px;
    }
</style>