<template>
    <div class="home">
        <!-- 页面布局 -->
        <el-container>
            <!-- 头部信息栏 -->
            <el-header>
              <span class="sy">欢迎{{loginName}}</span>
              <!-- <span class="sy">首页</span> -->
              <!-- <span class="lm">欢迎{{loginName}}</span> -->
              <!-- 退出按钮 -->
              <el-button type="primary" class="exit" @click="goBack">退出</el-button>
            </el-header>
            <el-container>
                <!-- 左侧导航 -->
                <Nav/>
                <!-- 主体信息部分 -->
                <el-main>
                  <router-view/>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
  import Nav from './Nav.vue'  //引入左侧导航菜单

  export default {
    data() {
      return {
        loginName:''
      }
    },
    components: {
        Nav  //引入组件
    },
    mounted(){
      //初始化加载，进入到该页面自动加载该方法
      this.init();
    },
    methods: {
      goBack(){
        sessionStorage.removeItem("username");
        this.$router.push("/login");
      },
      init(){
          // 获取登录用户名
          this.loginName = sessionStorage.getItem("name");
      }
    }
  }
</script>

<style>
  .home{
    height: 100vh;
  }

  .el-container.is-vertical{
    height: 100%;
  }

  .el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    /* text-align: center; */
    line-height: 60px;
  }
  
  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 200px;
  }
  
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }
  
  body > .el-container {
    margin-bottom: 40px;
  }
  
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }

  .home .lm{
    margin-left: 500px;
  }

  .home .exit{
    float: right;
    margin-top: 10px;
  }
</style>